function debounce(func, wait) {
  let timeout;

  return function () {
    let context = this;
    let args = arguments;

    clearTimeout(timeout);
    timeout = setTimeout(function () {
      func.apply(context, args);
    }, wait);
  };
}

const formatGameRange = (ttypeKey) => {
  const gameRange = ttypeKey.split("_");
  if (gameRange[1] === "0") return ttypes[gameRange[0]] + " - 练习场";
  return (
    ttypes[gameRange[0]] +
    " - " +
    gameRange[1] +
    "人" +
    " - " +
    formatCash(gameRange[2])
  );
};

const allTtypeFilters = () => {
  const res = [];
  const player = [0, 2, 6];
  const difen = [
    0, 1, 5, 10, 25, 50, 100, 200, 300, 500, 1000, 2000, 3000, 4000, 5000, 8000,
    10000,
  ];
  const poolDifen = [
    0, 2500, 5000, 10000, 25000, 50000, 100000, 200000, 300000,
  ];
  for (let i in ttypes) {
    if (i == 2) continue;
    for (let p of player) {
      for (let d of difen) {
        if (p === 0 && d !== 0) continue;
        if (d === 0 && p !== 0) continue;

        const key = i + "_" + p + "_" + d;
        res.push({ text: formatGameRange(key), value: key });
      }
    }
  }
  for (let p of player) {
    for (let d of poolDifen) {
      if (p === 0 && d !== 0) continue;
      if (d === 0 && p !== 0) continue;

      const key = 2 + "_" + p + "_" + d;
      res.push({ text: formatGameRange(key), value: key });
    }
  }
  // }

  return res;
};

