import React, { useEffect, useState } from "react";
import { Modal, Form, Select, message } from "antd";
import { addRobotGame } from "../../../api/robot";
import { ttypeArr } from "constants/ttypes";
import { robot_info_get, room_get_by_name } from "../../../api/room";

const { Option } = Select;

function EditForm(props) {
  const { data, onCancel, onSuccess } = props;
  const [form] = Form.useForm();
  const [ttypeValue, setTtypeValue] = useState("");

  const [room_list, set_room_list] = useState([]);
  const [state, set_state] = useState(0); //0为添加,1为修改
  const uid = data.uid;
  // console.log("uid", uid);
  const submit = async () => {
    const submitData = form.getFieldsValue();

    // console.log(form.getFieldValue("room_ids"),1234);
    const res = await addRobotGame(
      // uid,
      // ttypeKey,
      uid,
      form.getFieldValue("room_ids")?.toString()
    );
    if (res) {
      message.success(state == 0 ? "添加成功" : "修改成功");
      onSuccess();
      onCancel();
    } else {
      message.error(state == 0 ? "添加失败" : "修改失败");
    }

    form.resetFields();
    onCancel();
  };
  const trans_room_type = (room_type) => {
    let text;
    switch (room_type) {
      case 1:
        text = "13 Points";
        break;
      case 2:
        text = "10 Card";
        break;
      case 3:
        text = "Pool";
        break;
    }
    return text;
  };
  //获取房间列表
  const get_room_h = async () => {
    let data = await room_get_by_name();
    const list = data?.data?.map((item) => ({
      title: item.game_type,
      value: item.rid,
    }));

    // console.log(list);
    list && set_room_list(list);
  };
  const trans_room_ids = (ids) => {
    let ids_n;
    ids_n = ids.map((item) => parseInt(item));
    return ids_n;
  };
  //获取机器人详情
  const robot_info_get_h = async () => {
    let data = await robot_info_get({
      uid,
    });
    console.log("详情", data);
    if (data?.data?.room_ids) {
      // console.log(data.room_ids.split(","));
      form.setFieldsValue({
        room_ids: trans_room_ids(data?.data?.room_ids.split(",")),
      });
      set_state(1);
    } else {
      set_state(0);
    }
  };
  useEffect(() => {
    if (data.visible) {
      get_room_h();
      robot_info_get_h();
    }
  }, [data.visible]);
  return (
    <Modal
      visible={data.visible}
      onOk={submit}
      onCancel={onCancel}
      title={state == 0 ? "添加场次" : "修改场次"}
      okText="确定"
      cancelText="返回"
      maskClosable={false}
      closable={false}
      getContainer={false}
    >
      <Form
        form={form}
        name="form_in_modal"
        labelCol={{ span: 6 }}
        wrapperCol={{ span: 16 }}
      >
        <Form.Item label="房间" name="room_ids">
          <Select mode="multiple">
            {room_list.map((item) => (
              <Option key={item.value} value={item.value}>
                {item.title}
              </Option>
            ))}
          </Select>
        </Form.Item>
        {/* <Form.Item label="玩法" name="ttype">
          <Select onChange={(v) => setTtypeValue(v)}>
            {ttypeArr.map((v) => (
              <Option key={v["ttype"]} value={v["ttype"] + ""}>
                {v["name"]}
              </Option>
            ))}
          </Select>
        </Form.Item>

        <Form.Item label="人数" name="player">
          <Select>
            <Option value="0">练习场</Option>
            <Option value="2">2</Option>
            <Option value="6">6</Option>
          </Select>
        </Form.Item>

        {ttypeValue !== "2" && (
          <Form.Item label="底注" name="difen">
            <Select>
              <Option value="0">练习场</Option>
              <Option value="1">0.01</Option>
              <Option value="5">0.05</Option>
              <Option value="10">0.1</Option>
              <Option value="25">0.25</Option>
              <Option value="50">0.5</Option>
              <Option value="100">1</Option>
              <Option value="200">2</Option>
              <Option value="300">3</Option>
              <Option value="500">5</Option>
              <Option value="1000">10</Option>
              <Option value="2000">20</Option>
              <Option value="3000">30</Option>
              <Option value="4000">40</Option>
              <Option value="5000">50</Option>
              <Option value="8000">80</Option>
              <Option value="10000">100</Option>
            </Select>
          </Form.Item>
        )}

        {ttypeValue === "2" && (
          <Form.Item label="底注" name="difen">
            <Select>
              <Option value="0">练习场</Option>
              <Option value="2500">25</Option>
              <Option value="5000">50</Option>
              <Option value="10000">100</Option>
              <Option value="25000">250</Option>
              <Option value="50000">500</Option>
              <Option value="100000">1000</Option>
              <Option value="200000">2000</Option>
              <Option value="300000">3000</Option>
            </Select>
          </Form.Item>
        )} */}
      </Form>
    </Modal>
  );
}

export default React.memo(
  EditForm,
  (prev, next) => JSON.stringify(prev["data"]) === JSON.stringify(next["data"])
);
