import React, { useState } from 'react';
import { Modal, Form, Upload, message } from 'antd';
import { LoadingOutlined, PlusOutlined } from "@ant-design/icons";
import { setRobotPic } from 'api/robot'
import {useSelector} from "react-redux";
import {awsSave, awsUpload, awsUploadFile, downloadFromS3} from "../../../utils/aws";

function EditForm(props) {
  const { data, onCancel, onSuccess } = props;
  const [form] = Form.useForm();
  const [loading, setLoading] = useState(false);
  const [imageUrl, setImageUrl] = useState('');
  const [path, setPath] = useState('');

  const session = useSelector(state => state.session);
  const baseUrl = useSelector(state => state.baseUrl);

  const uid = data.uid;

  const submit = async () => {
    if (!path) return;

    const res = await setRobotPic({uid, pic: path});
    if (res) {
      message.success("修改成功");
      onSuccess()
    } else {
      message.error("修改失败");
    }

    onCancel();
  };

  const getBase64 = (img, callback) => {
    const reader = new FileReader();
    reader.addEventListener('load', () => callback(reader.result));
    reader.readAsDataURL(img);
  };

  const handleChange = info => {
    console.log('info', info)
    if (info.file.status === 'uploading') {
      setLoading(false);
      return;
    }
    if (info.file.status === 'done') {
      // Get this url from response in real world.
      getBase64(info.file.originFileObj, imageUrl => {
          setImageUrl(imageUrl);
          setLoading(false);
      });

      setPath(info.file.response.data)
    }
  };

  // const beforeUpload = file => {
  //   const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
  //   if (!isJpgOrPng) {
  //     message.error('You can only upload JPG/PNG file!');
  //   }
  //   const isLt2M = file.size / 1024 / 1024 < 2;
  //   if (!isLt2M) {
  //     message.error('Image must smaller than 2MB!');
  //   }
  //   return isJpgOrPng && isLt2M;
  // };

  const beforeUpload = async file => {
    let result = await awsUpload(file,file.name,0);
    //返回结果为false, 服务器上传
    if(result==="false"){
      return true
    }

    if(result == null ){
      return false
    }

    const reader = new FileReader()
    reader.readAsDataURL(file)
    reader.onload = () => {
      const _base64 = reader.result
      setImageUrl(_base64)  //将_base64赋值给图片的src，实现图片预览
    }
    setPath(result)

    return false
  };



  const uploadButton = (
    <div>
      {loading ? <LoadingOutlined /> : <PlusOutlined />}
      <div className="ant-upload-text">Upload</div>
    </div>
  );
  // const { imageUrl } = "";

  return (
    <Modal visible={data.visible} onOk={submit} onCancel={onCancel} title="修改头像" okText="确定" cancelText="返回" maskClosable={false} closable={false} getContainer={false}
    >
      <Upload
        name="avatar"
        listType="picture-card"
        className="avatar-uploader"
        showUploadList={false}
        // action={process.env.REACT_APP_BASE_URL + '/upload'}
        action={baseUrl + '/upload'}
        headers={{'x-token' :session.token}}
        beforeUpload={beforeUpload}
        onChange={handleChange}
      >
        {imageUrl ? <img src={imageUrl} alt="avatar" style={{ width: '100%' }} /> : uploadButton}
      </Upload>
    </Modal>
  );
}

export default React.memo(
  EditForm,
  (prev, next) => JSON.stringify(prev['data']) === JSON.stringify(next['data'])
);
// export default EditForm;
